* {
	box-sizing: border-box;
	-webkit-box-sizing: border-box;
	-moz-box-sizing: border-box;
	margin: 0;
	padding: 0;
	font-family: Arial, Helvetica, sans-serif;
	color: inherit;
}

html,
body {
	width: 100vw;
	height: 100vh;
}
a {
	color: var(--color-blue);
	&,
	&:link,
	&:visited {
		text-decoration: none;
		font-weight: bold;
	}
}


@mixin hide {
	@include absolute($top:-9999px,$left:-9999px,$important:true);	
}
.hide {
	@include hide;
	@include mq(mobile) {
		&-mobile {
			@include hide;
		}
	}
	@include mq(desktop) {
		&-desktop {
			@include hide;
		}
	}
}